import React, { useEffect, useState, useMemo } from "react";
import {
    Box,
    Button,
    Pagination,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    TextField,
    Tooltip,
    Typography,
} from "@mui/material";
import { useTranslation } from "react-i18next";
import { useAppState } from "../../state/stateContext";
import Highlight from "../../components/HighlightText"; // Import your Highlight component
import { CheckCircle } from "@mui/icons-material";

const ClientSelector = ({ onClientChange }) => {
    const { t } = useTranslation();
    const { clients, getClients, getClientsIsLoading, getClientsError } = useAppState();
    const [selectedClient, setSelectedClient] = useState(null);
    const [page, setPage] = useState(1);
    const [search, setSearch] = useState("");
    const itemsPerPage = 10;

    useEffect(() => {
        if (clients.length === 0) {
            getClients();
        }
    }, [getClients]);

    useEffect(() => {
        onClientChange(selectedClient);
    }, [selectedClient, onClientChange]);

    const handleSelectClient = (client) => {
        setSelectedClient(client);
    };

    const handleDeselectClient = () => {
        setSelectedClient(null);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleSearchChange = (event) => {
        setSearch(event.target.value);
        setPage(1);
    };

    const filteredClients = useMemo(() => {
        return clients.filter((client) =>
            client.companyName.toLowerCase().includes(search.toLowerCase())
        );
    }, [clients, search]);

    const currentPageClients = useMemo(() => {
        return filteredClients.slice((page - 1) * itemsPerPage, page * itemsPerPage);
    }, [filteredClients, page]);

    if (getClientsIsLoading) {
        return <Typography>{t("LoadingClients")}</Typography>;
    }

    if (getClientsError) {
        return <Typography color="error">{t("ErrorLoadingClients")}: {getClientsError}</Typography>;
    }

    return (
        <Box sx={{ my: 4, pb: 2 }}>
            {selectedClient ? (
                <Box sx={{ position: "relative" }}>
                    {/* CheckCircle icon for the selected client */}
                    <CheckCircle
                        color="success"
                        sx={{
                            position: "absolute",
                            top: -10,
                            left: -10,
                            fontSize: "2rem",
                        }}
                    />

                    {/* Selected Client Details */}
                    <TableContainer sx={{ border: "1px solid", borderColor: "success.main", borderRadius: 1 }}>
                        <Table size="small" aria-label="client details">
                            <TableBody>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {t("ClientName")}:
                                    </TableCell>
                                    <TableCell>{`${selectedClient.firstName} ${selectedClient.lastName}`}</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {t("CompanyName")}:
                                    </TableCell>
                                    <TableCell><strong>{selectedClient.companyName}</strong></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {t("VAT")}:
                                    </TableCell>
                                    <TableCell>{selectedClient.vatNumber}</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {t("Email")}:
                                    </TableCell>
                                    <TableCell>{selectedClient.email}</TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                    <Button variant="outlined" onClick={handleDeselectClient} sx={{ mt: 1 }}>
                        {t("DeselectClient")}
                    </Button>
                </Box>
            ) : (
                <>
                    {/* Search Filter */}
                    <TextField
                        label={t("SearchCompany")}
                        variant="outlined"
                        fullWidth
                        value={search}
                        onChange={handleSearchChange}
                        sx={{ mb: 1 }}
                        size="small"
                    />

                    {/* Client List */}
                    <TableContainer>
                        <Table size="small" aria-label="client table">
                            <TableHead>
                                <TableRow>
                                    <TableCell>{t('ID')}</TableCell>
                                    <TableCell>{t('CompanyName')}</TableCell>
                                    <TableCell>{t('VAT')}</TableCell>
                                    <TableCell>{t('Email')}</TableCell>
                                    <TableCell>{t('Actions')}</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {currentPageClients.map((client) => (
                                    <TableRow key={client.id}>
                                        <TableCell>{client.id}</TableCell>

                                        {/* Company Name with Tooltip and Highlight */}
                                        <TableCell>
                                            <Tooltip title={client.companyName} arrow>
                                                <Box
                                                    sx={{
                                                        overflow: 'hidden',
                                                        textOverflow: 'ellipsis',
                                                        whiteSpace: 'nowrap',
                                                        maxWidth: '120px',
                                                    }}
                                                >
                                                    <strong><Highlight text={client.companyName} search={search} /></strong>
                                                </Box>
                                            </Tooltip>
                                        </TableCell>

                                        {/* VAT with Tooltip */}
                                        <TableCell>
                                            <Tooltip title={client.vatNumber} arrow>
                                                <Box
                                                    sx={{
                                                        overflow: 'hidden',
                                                        textOverflow: 'ellipsis',
                                                        whiteSpace: 'nowrap',
                                                        maxWidth: '120px',
                                                    }}
                                                >
                                                    {client.vatNumber}
                                                </Box>
                                            </Tooltip>
                                        </TableCell>

                                        {/* Email with Tooltip and Highlight */}
                                        <TableCell>
                                            <Tooltip title={client.email} arrow>
                                                <Box
                                                    sx={{
                                                        overflow: 'hidden',
                                                        textOverflow: 'ellipsis',
                                                        whiteSpace: 'nowrap',
                                                        maxWidth: '120px',
                                                    }}
                                                >
                                                    <Highlight text={client.email} search={search} />
                                                </Box>
                                            </Tooltip>
                                        </TableCell>

                                        <TableCell>
                                            <Button
                                                variant="contained"
                                                size="small"
                                                onClick={() => handleSelectClient(client)}
                                            >
                                                {t('Select')}
                                            </Button>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>

                    {/* Pagination */}
                    <Box sx={{ display: "flex", justifyContent: "center", p: 1 }}>
                        <Pagination
                            size="small"
                            count={Math.ceil(filteredClients.length / itemsPerPage)}
                            page={page}
                            onChange={handleChangePage}
                        />
                    </Box>
                </>
            )}
        </Box>
    );
};

export default ClientSelector;