import React, { useState, useEffect } from "react";
import { FormControl, FormControlLabel, FormLabel, Radio, RadioGroup, TextField, Typography } from "@mui/material";
import { useTranslation } from "react-i18next";
import ReactMarkdown from 'react-markdown';
import MarkdownEditor from '../../components/MarkdownEditor';

const EMPTY_QUOTE_DETAILS = {
  company: "",
  object: "",
  description: "",
};

const QuoteDetails = ({ onDetailsChange }) => {
  const { t } = useTranslation();
  const [quoteHeadDetails, setQuoteHeadDetails] = useState(EMPTY_QUOTE_DETAILS);
  const { company, object, description } = quoteHeadDetails;

  useEffect(() => {
    onDetailsChange(quoteHeadDetails);
  }, [quoteHeadDetails, onDetailsChange]);

  return (
    <>
      <Typography variant="h6" gutterBottom>
        {t("QuoteDetails")}
      </Typography>
      <FormControl component="fieldset" margin="normal">
        <FormLabel component="legend">{t("Company")}</FormLabel>
        <RadioGroup
          row
          name="company"
          value={company}
          onChange={(e) =>
            setQuoteHeadDetails({
              ...quoteHeadDetails,
              [e.target.name]: e.target.value,
            })
          }
        >
          <FormControlLabel
            value="Sermixer"
            control={<Radio />}
            label="Sermixer"
          />
          <FormControlLabel
            value="S2 Truck Service"
            control={<Radio />}
            label="S2 Truck Service"
          />
        </RadioGroup>
      </FormControl>
      <TextField
        label={t("Object")}
        variant="outlined"
        fullWidth
        name="object"
        margin="normal"
        value={object}
        onChange={(e) =>
          setQuoteHeadDetails({
            ...quoteHeadDetails,
            [e.target.name]: e.target.value,
          })
        }
      />
      <Typography variant="h6" gutterBottom>
        {t("DescriptionOffer")}
      </Typography>
      <MarkdownEditor
        value={description}
        onChange={(value) =>
          setQuoteHeadDetails({
            ...quoteHeadDetails,
            description: value,
          })
        }
      />
    </>
  );
};

export default QuoteDetails;
